<template>
  <div class="line-charts">
    <div :id="id" class="charts"></div>
  </div>
</template>

<script>
import echarts from 'echarts';

/**
 * @曲线图
 * @author：risheng.huang
 * @参数说明：
 * lineChartData:{
  *   xNameData: [],  X轴的信息
  *   实际数据的展示数据。对象的属性为：统计的维度信息名；属性值为：对应x轴的信息的各个数值信息。
  *   dataMap: {
  *      '本月':[12,45,66,90,99,88],
  *      '上月':[23,45,77,88,89,100]
  *   },
  *   维度的颜色展示信息
  *   colorMap:['本月':['#0088d1', '#df896d']}] ,
  *   xyName: [{ name: '日期', color: "#808080", }, { name: '坐席数', color: '#808080' }]  //第一个颜色x轴，第二个为y轴
  * }
 * yDw： y轴（数据值）的单位，如 %，万，千万 等
 * legendStatus ： 是否显示图例，默认显示,
 * maxPercent :数值是否最大为100%,默认为否,不设置最大值
 * */
export default {
  name: "line-charts",
  props: {
    lineChartData: {
      type: Object,
      default: () => {
        return {}
      }
    },

    yDw: {
      type: String,
      default: () => {
        return ''
      }
    },
    nameXs: {
      type: String,
      default: () => {
        return ''
      }
    },
    legendStatus: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    maxPercent: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    isAreaStyle: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    isSmooth: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    name: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data() {
    return {
      id: Math.random() + '_',
      lineData: [],
      xData: [],
      legend: [],
      series: [],
      xyNameData: []
    }
  },
  watch: {},
  created() {
    this.xData = this.lineChartData.xNameData ? this.lineChartData.xNameData : [];
    this.xyNameData = this.lineChartData.xyName ? this.lineChartData.xyName : []
  },
  mounted() {
    this.dataFilter(this.lineChartData);
    this.init();
  },
  methods: {
    dataFilter(data) {
      console.log(data, 'data');
      let series = [];
      let dataMap = data.dataMap
      if (dataMap) {
        for (let key in dataMap) {
          let color = data.colorMap[key];
          this.legend.push(key);
          series.push({
            name: key,
            type: 'line',
            symbol: 'circle',
            symbolSize: 10,
            smooth: this.isSmooth,
            label: {
              show: true,
              position: 'top',
              color: color[0]
            },
            areaStyle: this.isAreaStyle ? {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0, 0, 0, 1,
                  [
                    { offset: 0, color: color[0] },
                    { offset: 1, color: 'rgba(255,255,255,0.5)' }
                  ]
                )
              }
            } : {},
            itemStyle: {
              shadowColor: color[0],
              // shadowBlur: 20,
              normal: {
                color: "#fff",
                borderColor: color[0],
                borderWidth: 4,
                shadowColor: color[0],
                lineStyle: {
                  color: color[0],
                  shadowColor: color[0],
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  opacity: 1,
                  shadowBlur: 8,
                  type: "solid",
                  width: 4
                },
              }
            },
            data: dataMap[key]
          });
        }
      }

      this.series = series;
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let dw = this.yDw;
      let xs = this.nameXs;

      let options = {
        grid: {
          left: 50,
          right: 60
        },
        tooltip: {
          show: true,
          trigger: 'axis',
          // formatter: function (a) {
          //   let info = '';
          //   let _i = 0;
          //   for (let item of a) {
          //     _i++;
          //     if (a.length > 1) {
          //       info += item.seriesName
          //     }
          //     info += item.axisValueLabel + xs + '：' + item.value.toLocaleString() + dw;
          //     if (_i < a.length) {
          //       info += '<br>'
          //     }
          //   }
          //   return info;
          // }
        },
        legend: {
          color: this.lineChartData.color,
          show: this.legendStatus,
          x: 'right',
          icon: 'stack',
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            fontFamily: 'Noto Sans SC',
            color: '#fff',
          },
          data: this.legend
        },
        xAxis: {
          data: this.xData,
          name: this.xyNameData[0].name,
          nameTextStyle: {
            verticalAlign: "top",
            // align: "center",
            padding: [8, 0, 0, 0],
            color: this.xyNameData[0].color
          },
          axisLabel: {
            interval: 0,
            show: true,
            fontSize: this.fontSize,
            textStyle: {
              fontSize: this.fontSize,
              color: this.xyNameData[0].color,
            },
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: '#000'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#d9f0f2',
            },

          }
        },
        yAxis: {
          type: 'value',
          name: this.xyNameData[1].name,
          nameGap: 30,
          nameTextStyle: {
            align: "right",
            color: this.xyNameData[1].color
          },
          // max: this.percentRange[0],
          // min: this.percentRange[1],
          splitLine: {
            show: true,
            lineStyle: {
              color: '#d9f0f2'
            }
          },
          axisLine: {
            show: false,
            lineStyle: {
              color: '#d9f0f2'
            }
          },
          axisLabel: {
            show: true,
            fontSize: this.fontSize,
            formatter: function (value) {
              return value + dw
            },
            color: this.xyNameData[1].color,
            interval: 0,
            rotate: 0
          }
        },
        series: this.series
      };
      if (this.maxPercent) options.yAxis[0].max = 100
      console.log(options, 'optionsline');
      charts.setOption(options)
      this.$emit('setOption', this.name, charts,)
    }
  }
}
</script>

<style scoped lang="scss">
.line-charts {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>